.container {
  width: 100%;
  height: 100vh;
  background-image: url('https://i.postimg.cc/HnBzHggc/fondo-searchbatr.jpg');
  background-repeat: no-repeat; /* Evita la repetición de la imagen */
  background-position: bottom; /* Posiciona la imagen en la parte inferior */
}

  .conatiner__main {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
  }

  .forms__info {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    width: 400px;
    border-radius: 20px;
    box-shadow: 2px 3px 5px 3px rgb(145, 143, 143);
    background-color: rgba(243, 220, 118, 0.781)
  }
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .danger {
    color: red;
    font-size: 10px;
  }
  
  input.danger {
    border-bottom: 2px solid red;
  }
  
  .forms__info input {
    border-style: none;
    height: 30px;
    border-radius: 3px;
    border-bottom: solid 1px;
    color: grey;
    margin-bottom: 10px;
    width: 300px;
    outline: none;
  }
  
  .forms__info input:hover {
    border-bottom: solid 1px;
    border-color: #f5a055;
    transition: 0.1s linear;
  }
  .forms__info select {
    border-style: none;
    height: 30px;
    border-radius: 3px;
    border-bottom: solid 1px;
    color: grey;
    margin-bottom: 10px;
    width: 300px;
    outline: none;
  }
  .forms__info select:hover {
    border-bottom: solid 1px;
    border-color: #f5a055;
    transition: 0.1s linear;
  }
  
  .forms__info textarea {
    border-style: none;
    height: 30px;
    border-radius: 3px;
    border-bottom: solid 1px;
    color: grey;
    outline: none;
  }
  
  .forms__info textarea:hover {
    outline: none;
    border-bottom: solid 1px;
    border-color: #f5a055;
  }
  
  .boton {
    background-color: orange;
    cursor: pointer;
  }
  .boton input {
    color: white;
  }
  .boton__add input {
    background-color: orange;
    color: white;
    opacity: 0.5;
    margin-bottom: 15px;
    align-items: center;
    cursor: pointer;
    font-weight: bold;
  }
  .boton__new input {
    background-color: orange;
    color: white;
  
    margin-bottom: 15px;
    align-items: center;
    cursor: pointer;
    font-weight: bold;
  }
  .logo_bootom {
    width: 400px;
  }
  h1 {
    color: #f5a055;
    margin-right: 15px;
  }
  .score {
    display: flex;
    color: orange;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .boton__add {
    align-items: center;
  }
  .diet {
    width: 300px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    max-height: 90px;
    overflow-y: auto;
  }
  .chip {
    min-height: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #f5a055;
    border-radius: 7px;
    padding: 0 4px;
    margin: 2px;
    box-shadow: 3px 3px 5px rgb(145, 143, 143);
  }
  ul li {
    list-style: none;
    color: white;
    height: 25px;
    margin-bottom: 3px;
    margin: 5px;
  }
  .buton__x {
    font-weight: bold;
    color: white;
    font-size: 20px;
    height: 25px;
    width: 25px;
    cursor: pointer;
    margin-bottom: 10px;
    border: none;
    background-color: transparent;
  }
  
  .d__back {
    float: left;
    width: 70px;
    height: 50px;
    border-radius: 0px 20px 20px 0;
    background-color: #fb982f;
    color: white;
  font-weight: bold;
  }